import s from "./AdminLoginPass.module.css"

import React, {useState} from 'react';
import {useDispatch} from "react-redux";
import {closeAdminLoginPopup, showAdminPopup} from "../../../redux/reducers/popupsReducer";
import Button from "../../globalComponents/Button/Button";

function AdminLoginPass(props) {
    const showAdminPopupCallback = () => {
        if(login === "edgar" && pass === "1111"){
            dispatch(closeAdminLoginPopup())
            dispatch(showAdminPopup())
        }
        else{
            alert("You are Not Admin")
            setLogin("")
            setPass("")
        }
    }
    const dispatch = useDispatch()
    const [login,setLogin] = useState("")
    const [pass,setPass] = useState("")
    const closePopup = () => {
        dispatch(closeAdminLoginPopup())
    }
    return (
        <div className={s.wrapper}>
            <div className={s.block}>
                <span
                    className={s.close}
                    onClick={() => {closePopup()}}
                >x</span>
                <h2>Login</h2>
                <div className={s.loginFieldsBlock}>
                    <div>
                        <input type="text" placeholder={"Login"} value={login} onChange={(e) => {
                            setLogin(e.target.value)
                        }}/>
                    </div>
                    <div>
                        <input type="password" placeholder={"Password"} value={pass} onChange={(e) => {
                            setPass(e.target.value)
                        }}/>
                    </div>
                    <div>
                        <Button text={"Login"} color={"generateColor"} callback={showAdminPopupCallback}/>
                    </div>
                </div>

            </div>
        </div>
    );
}

export default AdminLoginPass;