import {doc, getFirestore, updateDoc, arrayUnion} from "firebase/firestore";

async function setVoters(voter){
    const db = getFirestore()
    const voterRef = doc(db, "voters", "voters");

    await updateDoc(voterRef, {
        voters: arrayUnion(Math.floor(Math.random() * 99 ) + 10 + voter.name + " " + voter.surName)
    });
}

export default setVoters